@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
::selection {
  background: #a259ff;
  color: #fff;
}
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
}
html {
  font-size: 62.5%;
}
body {
  box-sizing: border-box;
  background-color: #f0f0f0;
  width: 85%;
  margin: 0 auto;
  padding: 2rem 0;
  font-family: "Roboto", sans-serif;
}

.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 1rem;
}

.nav__logo > a {
  font-size: 2rem;
  font-weight: bold;
}

.nav__logo--mobile {
  display: none;
}

.nav__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.nav__item {
  font-size: 1.5rem;
  color: #fff;
  padding: 0.5rem 1rem;
  cursor: pointer;
}

.nav__item > a {
  text-decoration: none;
  color: #000;
}

.nav__item:hover {
  color: #000;
}

.selected {
  border-bottom: 2px solid #e16259;
}

@media screen and (max-width: 690px) {
  .nav__logo > a {
    font-size: 2rem;
    font-weight: bold;
  }
  .nav__logo--default {
    display: none;
  }
  .nav__logo--mobile {
    display: block;
  }
}
/*# sourceMappingURL=Header.css.map */
.user__info {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 1fr 3fr;
      grid-template-columns: 1fr 3fr;
  align-items: center;
  grid-gap: 4rem;
  gap: 4rem;
  width: 50%;
  margin: 10rem auto;
  background-color: #fff;
  border-radius: 1.6rem;
  padding: 3rem;
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.1);
}

.user__info-image {
  border-radius: 50%;
  overflow: hidden;
  margin: 0 auto;
  margin-bottom: 1.6rem;
}

.user__info-image > img {
  width: 10rem;
  height: 10rem;
}

.user__info-details {
  font-size: 1.6rem;
  line-height: 2.5rem;
}

.user__info-name {
  font-size: 3.3rem;
  font-weight: bold;
  margin-bottom: 1.6rem;
  text-decoration: underline;
  color: #e16259;
}

.user__info-gender {
  color: rgba(0, 0, 0, 0.6);
  text-transform: capitalize;
}

@media screen and (max-width: 1400px) {
  .user__info {
    width: 60%;
  }
}

@media screen and (max-width: 980px) {
  .user__info {
    width: 80%;
  }
  .user__info-name {
    font-size: 3rem;
  }
}

@media screen and (max-width: 680px) {
  .user__info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin: 5rem auto;
  }
  .user__info-name {
    font-size: 2.5rem;
  }
  .user__info-image {
    margin-bottom: 1rem;
  }
}
/*# sourceMappingURL=UserInfo.css.map */
.user__card {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 2rem;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  border-radius: 0.4rem;
  background-color: #fff;
  box-shadow: 0px 2px 7px rgba(0, 0, 0, 0.2);
}

.user__card .user__gender {
  text-transform: capitalize;
}

.user__card-top {
  font-size: 1.4rem;
  color: rgba(0, 0, 0, 0.6);
}

.user__card-middle {
  font-weight: bold;
  font-size: 2.2rem;
}

.user__card-bottom {
  color: #e16259;
  font-size: 1.5rem;
}

.user__card:hover {
  box-shadow: 0 8px 10px -5px rgba(0, 0, 0, 0.1), 0 4px 4px -2px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  background-color: #a259ff;
}

.user__card:hover > p {
  color: #fff;
}

.user__card--selected {
  background-color: #a259ff;
}

.user__card--selected > p {
  color: #fff !important;
}
/*# sourceMappingURL=UserCard.css.map */
.users {
  width: 100%;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: (1fr)[4];
      grid-template-columns: repeat(4, 1fr);
  grid-gap: 5rem;
  gap: 5rem;
  margin: 4rem 0;
}

@media screen and (max-width: 1400px) {
  .users {
    -ms-grid-columns: (1fr)[3];
        grid-template-columns: repeat(3, 1fr);
  }
}

@media screen and (max-width: 1080px) {
  .users {
    -ms-grid-columns: (1fr)[2];
        grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (max-width: 690px) {
  .users {
    -ms-grid-columns: 1fr;
        grid-template-columns: 1fr;
  }
}
/*# sourceMappingURL=Users.css.map */
